'use client';

import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import { fetchBooks } from '@/API/booksApi';
import { log } from 'console';

interface BookAttributes {
  slug: string;
  author: string;
  cover: string;
  dedication: string;
  pages: number;
  release_date: string;
  summary: string;
  title: string;
  wiki: string;
}

interface Book {
  id: string;
  type: string;
  attributes: BookAttributes;
  relationships: {
    chapters: {
      data: Array<{ id: string; type: string }>;
    };
  };
  links: {
    self: string;
  };
}

export default function Books() {
  const [books, setBooks] = useState<Book[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  const fetchAllBooks = async () => {
    console.log('Fetching books...');  // Log when fetch is called
    try {
      const booksData = await fetchBooks();
      console.log('Fetched books:', booksData);  // Log the response data
      setBooks(booksData);
      log(booksData);
    } catch (error) {
      console.error('Error fetching books:', error);
      setError('Failed to fetch books');
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    

    fetchAllBooks();
  }, []);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>{error}</p>;
  }

  return (
    <main className="min-h-screen w-full flex items-center justify-center bg-background flex-col gap-5">
      <h1 className="text-2xl">Welcome to the Books Page üìö</h1>

      {books.length > 0 ? (
        books.map((book) => (
          <div key={book.id} className="book-item">
            <img src={book.attributes.cover} alt={book.attributes.title} className="book-cover" />
            <h2>{book.attributes.title}</h2>
            <p>{book.attributes.author}</p>
            <p>{book.attributes.summary}</p>
            <Link href={book.attributes.wiki}>
              <a target="_blank" className="text-blue-500">Learn more on Wiki</a>
            </Link>
            <p>Pages: {book.attributes.pages}</p>
            <p>Release Date: {book.attributes.release_date}</p>
            <h3>Chapters:</h3>
            <ul>
              {book.relationships.chapters.data.map((chapter, index) => (
                <li key={chapter.id}>Chapter {index + 1} (ID: {chapter.id})</li>
              ))}
            </ul>
          </div>
        ))
      ) : (
        <p>No books available</p>
      )}

      <Link href="/">
        <button className="bg-white text-black px-4 py-2 rounded-md hover:bg-slate-200 duration-300 ease-in-out">
          Go Back üè†
        </button>
      </Link>
    </main>
  );
}
